.tile {
  display: -ms-flexbox;
  display: flex;
  width: 35vw;
  height: 35vw;
  margin: 3px;
  font-size: .8rem;
  padding: 10px;
  -webkit-transition: all 3s ease;
  -o-transition: all 3s ease;
  transition: all 3s ease;
  background-size: cover;
}

.tile:hover {
  -webkit-animation: 2s flipcard alternate ease-in-out;
          animation: 2s flipcard alternate ease-in-out;
}

.tile.back {
  display: block;
  /*transform: rotateX(180deg);
  animation: 2s flipcard-back alternate ease-in-out;*/
}

.tile > h1 {
  font-size: 1.4rem;
  -ms-flex-item-align: end;
      align-self: flex-end;
  margin: 0;
}

.tile > h2 {
  font-size: 1rem;
}

a {
  text-decoration: none;
  color: black;
}

@-webkit-keyframes flipcard {
  0% {
    -webkit-transform: rotateX(0deg);
            transform: rotateX(0deg);
  }

  50% {
    color: transparent;
  }
  
  100% {
    -webkit-transform: rotateX(180deg);
            transform: rotateX(180deg);
  }
}

@keyframes flipcard {
  0% {
    -webkit-transform: rotateX(0deg);
            transform: rotateX(0deg);
  }

  50% {
    color: transparent;
  }
  
  100% {
    -webkit-transform: rotateX(180deg);
            transform: rotateX(180deg);
  }
}


